.container {
  max-width: 1110px;
  width: 100%;
  margin: 0 auto;
}
html {
  height: 100%;
  box-sizing: content-box;
}
body {
  font-family: 'Inter', 'Arial', sans-serif;
}
.header-block__icon {
  list-style: none;
  padding-left: 22px;
}
.header-block__link {
  display: flex;
  transition: 0.5s ease;
}
.header-block__link:hover {
  opacity: 0.5;
}
.header-block__list {
  display: flex;
}
.header-block__wrapper {
  display: flex;
  justify-content: space-between;
  font-weight: 400;
}
.header-block__box {
  display: flex;
  padding: 0;
}
.header-block__inner {
  padding: 0;
}
.header-block__link--basket {
  position: relative;
}
.header-block__link--basket::after {
  position: absolute;
  content: "";
  display: block;
  background-color: #000000;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  right: -4px;
  top: -6px;
}
.header-block__count {
  position: absolute;
  right: 0px;
  top: -5px;
  z-index: 1;
  font-size: 12px;
  line-height: 14px;
  color: #FFFFFF;
  font-weight: 400;
}
.search-header {
  list-style: none;
}
.search-header__submit {
  background-color: transparent;
  border: none;
  padding: 0;
}
.search-header__enter {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #9F9F9F;
  outline: none;
  margin-left: 20px;
}
.search-header__enter:hover {
  border-color: #000000;
}
.search-header__enter:focus {
  border-color: #000000;
}
.header-bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
.header-bottom-list {
  list-style: none;
}
.header-bottom__link {
  text-decoration: none;
  font-weight: 700;
  color: #9F9F9F;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  transition: 0.5s ease;
}
.header-bottom__link:hover {
  color: #000000;
}
.breadcrumbs-block__item {
  list-style: none;
  padding-right: 10px;
}
.breadcrumbs-block__list {
  display: flex;
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
}
.breadcrumbs-block__noactive {
  color: #9F9F9F;
  text-decoration: none;
  pointer-events: none;
}
.breadcrumbs-block__link {
  text-decoration: none;
  color: #000000;
}
